import React, { useEffect, useState } from 'react';
import {
    View,
    Text,
    StyleSheet,
    ScrollView,
    Image,
    TouchableOpacity,
    Platform
} from 'react-native';
import { OnlineUser } from '../services/WebSocket';
import webSocketService from '../services/WebSocket';
import { useNavigation } from '@react-navigation/native';
import { getImageUrl } from '../services/api';

const OnlineUsers: React.FC = () => {
    const [onlineUsers, setOnlineUsers] = useState<OnlineUser[]>([]);
    const navigation = useNavigation();

    useEffect(() => {
        const fetchOnlineUsers = () => {
            webSocketService.send({
                type: 'social',
                action: 'get_online_users'
            });
        };

        //      
        webSocketService.addMessageHandler('social', async (data) => {
            if (data.action === 'get_online_users' && data.users) {
                const usersWithAvatars = await Promise.all(
                    data.users.map(async (user) => ({
                        ...user,
                        avatar: await getImageUrl(user.avatar)
                    }))
                );
                setOnlineUsers(usersWithAvatars);
            }
        });

        //     
        fetchOnlineUsers();

        //    30 
        const interval = setInterval(fetchOnlineUsers, 30000);

        return () => {
            clearInterval(interval);
            webSocketService.removeMessageHandler('social');
        };
    }, []);

    const handleUserPress = (username: string) => {
        navigation.navigate('Profile', { username });
    };

    if (onlineUsers.length === 0) return null;

    return (
        <View style={styles.container}>
            <Text style={styles.title}> </Text>
            <ScrollView 
                horizontal 
                showsHorizontalScrollIndicator={false}
                style={styles.scrollView}
            >
                {onlineUsers.map((user) => (
                    <TouchableOpacity
                        key={user.id}
                        style={styles.userContainer}
                        onPress={() => handleUserPress(user.username)}
                    >
                        <View style={styles.avatarContainer}>
                            <Image
                                source={{ uri: user.avatar || undefined }}
                                style={styles.avatar}
                            />
                            <View style={styles.onlineIndicator} />
                        </View>
                        <Text style={styles.username} numberOfLines={1}>
                            {user.username}
                        </Text>
                    </TouchableOpacity>
                ))}
            </ScrollView>
        </View>
    );
};

const styles = StyleSheet.create({
    container: {
        backgroundColor: '#fff',
        paddingVertical: 10,
        borderBottomWidth: 1,
        borderBottomColor: '#eee',
        ...Platform.select({
            ios: {
                shadowColor: '#000',
                shadowOffset: { width: 0, height: 1 },
                shadowOpacity: 0.1,
                shadowRadius: 1,
            },
            android: {
                elevation: 2,
            },
        }),
    },
    title: {
        fontSize: 16,
        fontWeight: '600',
        color: '#000',
        marginLeft: 15,
        marginBottom: 10,
    },
    scrollView: {
        paddingHorizontal: 10,
    },
    userContainer: {
        alignItems: 'center',
        marginHorizontal: 5,
        width: 70,
    },
    avatarContainer: {
        position: 'relative',
        marginBottom: 5,
    },
    avatar: {
        width: 50,
        height: 50,
        borderRadius: 25,
        backgroundColor: '#f0f0f0',
    },
    onlineIndicator: {
        position: 'absolute',
        bottom: 0,
        right: 0,
        width: 12,
        height: 12,
        borderRadius: 6,
        backgroundColor: '#4CAF50',
        borderWidth: 2,
        borderColor: '#fff',
    },
    username: {
        fontSize: 12,
        color: '#666',
        textAlign: 'center',
    },
});

export default OnlineUsers; 